import React from 'react';
import styled from 'styled-components';
import Card from '../card/card';

const FlexBase = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1 0 100%;
    justify-content: center;
    margin: auto;
    max-width: 1280px;
    margin-bottom: 50px;
    padding: 0;
    @media (min-width: 480px) { padding: 0% 2%; }
    @media (min-width: 720px) { justify-content: flex-start; padding: 0% 2%;}
`;

const Grid = ({ source, blank, CardsPerColumn, PostInfo, PostTitle
}) => (
        <FlexBase>
            <Card 
                CardHyperlink="https://dribbble.com/shots/4122760-New-website-sneaky-peak"
                PostInfo="by RI Design Team on Jan 19, 2018" 
                PostTitle="New website sneaky peak" 
                image="https://cdn.dribbble.com/users/1892802/screenshots/4122760/artboard.png" 
                loadingMessage="Loading"
                source="dribbble" 
                isLoading="true"
            />
            <Card 
                CardHyperlink="https://dribbble.com/shots/4122585-Gradient-Styles"
                PostInfo="by RI Design Team on Jan 19, 2018" 
                PostTitle="Gradient Styles" 
                image="https://cdn.dribbble.com/users/1892802/screenshots/4122585/artboard.png" 
                loadingMessage="Loading"
                source="dribbble" 
            />
            <Card source="twitter" 
                CardHyperlink="https://twitter.com/ri_designteam/status/942725895130886145"
                PostAuthor="RightIndem Design"
                PostAvatar="https://pbs.twimg.com/profile_images/931789080744136705/MuZJH_q4_400x400.jpg"
                PostContent="The RightIndem design team have done a fantastic job testing colour variations of our new logo. Now for some decisions to be made #design #designtesting"
                PostHandle="@ri_designteam"
                PostTime="18 Dec 2017" 
                image="https://pbs.twimg.com/media/DRU77OiX0AARDKP.jpg:large" 
            />
            <Card 
                CardHyperlink="https://medium.com/@rightindem/rebranding-rightindem-de1383823ed2"
                PostContent="The RightIndem rebrand reflects our focus, vision and values. Our goal is to deliver a delightful, seamless experience that changes the landscape of an industry that is overshadowed by latency, bloated and legacy systems which cause user pain, discomfort and frustration. We approached our rebrand in the same way we approach our product work by identifying the goals we wish to accomplish. Due to our existing market presence, we set out to evolve our existing logo, building on existing rationale and brand personality. In addition, we wanted to introduce elements that accurately reflect our personality and culture, showcasing the position of RightIndem today." 
                PostTitle="Rebranding RightIndem" 
                image="https://cdn-images-1.medium.com/max/800/1*cy1ROI-LPIqTwLn9JNIjSQ.jpeg"
                source="medium" 
            />
            <Card 
                CardHyperlink="https://dribbble.com/shots/4112365-User-Journey-Comic"
                PostInfo="by RI Design Team on Jan 17, 2018" 
                PostTitle="User Journey Comic" 
                image="https://cdn.dribbble.com/users/1892802/screenshots/4112365/artboard.png" 
                loadingMessage="Loading"
                source="dribbble" 
            />
            <Card source="twitter" 
                CardHyperlink="https://twitter.com/ri_designteam/status/925339678772953088"
                PostAuthor="RightIndem Design"
                PostAvatar="https://pbs.twimg.com/profile_images/931789080744136705/MuZJH_q4_400x400.jpg"
                PostContent="Some excellent new design books hitting the design shelf in the RI Office. @thepixelgrid will love these"
                PostHandle="@ri_designteam"
                PostTime=" 31 Oct 2017" 
                image="https://pbs.twimg.com/media/DNd3awEXkAA3ML3.jpg:large" 
            />
        </FlexBase>
);

export default Grid;


