import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Heading from '../global/Heading';
import TextTruncate from 'react-text-truncate'; 

const Container = styled.div`
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 15px;
    width: 100%;
`;

const Avatar = styled.img`
    height: 48px;
    margin-right: 10px;
    display: flex;
    flex: 0 0 48px;
`;

const AccountInfo = styled.div`
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
`;

const Author = styled.div`
    font-size: 18px;
    font-weight: bold;
    display: flex;
    flex: 0 1 auto;
`;

const Time = styled.div`
    font-size: 14px;
    color: #657786;
    display: flex;
    flex: 1 0 auto;
    align-self: flex-start;
    justify-content: flex-end;
`;

const Handle = styled.span`
    font-size: 14px;
    color: #657786;
`;

const TwitterCard = ({ source, image, PostContent, PostAuthor, PostHandle, PostTime, PostAvatar }) => (
    <div>
        <Container>
            <Avatar src={PostAvatar}/>
            <AccountInfo>
                <Author>{PostAuthor}</Author>
                <Handle>{PostHandle}</Handle>
            </AccountInfo>
            <Time>{PostTime}</Time>
        </Container>
        <Heading source={source} style={{marginBottom: 0}}><TextTruncate line={3} truncateText="" text={PostContent} /></Heading>
    </div>
);

TwitterCard.propTypes = {
    source: PropTypes.oneOf(['twitter', 'medium', 'dribbble', 'other', 'blank', 'video']),
}

TwitterCard.defaultProps = {
    source: 'twitter',
};

export default TwitterCard;


