import React from 'react';
import PropTypes from 'prop-types';
import Heading from '../global/Heading';
import Description from '../global/Description';
import TextTruncate from 'react-text-truncate'; 

const MediumCard = ({ source, PostTitle, PostContent }) => (
    <div>
        {/* <Medium username="1d8b0af39629a9cc7b64e2e1f21d6301e12feff6bcdc114de018b97e8d7a1c211" /> */}
        <Heading>{PostTitle}</Heading> 
        <Description><TextTruncate line={5} truncateText="" text={PostContent} /></Description>
    </div>
);


MediumCard.propTypes = {
    source: PropTypes.oneOf(['twitter', 'medium', 'dribbble', 'other', 'blank', 'video']),
};

MediumCard.defaultProps = {
    source: 'medium',
};

export default MediumCard;


