import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Heading from '../global/Heading';

const DribbblePostInformation = styled.p`
    color: #999;
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 0;
`;

const DribbbleCard = ({ source, image, PostTitle, PostInfo }) => (
    <div>
        <Heading source={source}>{PostTitle}</Heading>
        <DribbblePostInformation>{PostInfo}</DribbblePostInformation>
    </div>
);

DribbbleCard.propTypes = {
    source: PropTypes.oneOf(['twitter', 'medium', 'dribbble', 'other', 'blank', 'video']),
}

DribbbleCard.defaultProps = {
    source: 'dribbble',
};

export default DribbbleCard;


