import styled from 'styled-components';

const Heading = styled.h2`
    font-size: 24px;
    margin-top: 0;
    margin-bottom: 0;
    ${props => (props.source === "twitter" && `margin-bottom: 15px;font-weight: 400;font-size: 18px;`)}
    ${props => (props.source === "video" && `font-size: 18px;`)}
`;

export default Heading;


