import React, { Component } from 'react';
import './index.css';
import Grid from './grid/grid';
import Hero from './hero/hero';
import SiteWrapper from './global/SiteWrapper';
import Footer from './footer/footer';
import MetaTags from 'react-meta-tags'; 

class App extends Component {
  render() {
    return (
      <div className="App">
        <SiteWrapper>
            <MetaTags>
                <title>RightIndem Design</title>
                <meta name="description" content="A curated list of design niceties, resources and articles from the RightIndem Design Team." />
                <meta property="og:url" content="http://www.nytimes.com/2015/02/19/arts/international/when-great-minds-dont-think-alike.html" />
                <meta property="og:type" content="article" />
                <meta property="og:title" content="RightIndem Design" />
                <meta property="og:description" content="A curated list of design niceties, resources and articles from the RightIndem Design Team." />
                <meta property="og:image" content="https://Rightindem.design" />
              </MetaTags>
            <Hero />
            <Grid />
            <Footer />
        </SiteWrapper>
      </div>
    );
  }
}

export default App;



