import React from 'react';
import PropTypes from 'prop-types';
import styled, { keyframes } from 'styled-components';

const Overlay = styled.div`
    align-items: center;
    background-color: #fff;
    background: radial-gradient(ellipse at center, rgba(255,255,255,1) 50%,rgba(255,255,255,0.95) 100%);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex: 0 0 auto;
    height: 100%;
    justify-content: center;
    position: absolute;
    width: 100%;
    z-index: 9;
`;

const spin = keyframes`
    0% {
        transform: none;
        will-change: transform;
    }
    100% {
        transform: rotate(360deg);
        will-change: transform;
    }
`;

const SpinGrad = styled.circle`
    transform-origin: 50% 50%;
    ${props => (props.aniType === "CSS" && `
        animation: ${spin} ${props.aniDur}s linear infinite;
        will-change: transform;
    `)}
`;

const LoadingText = styled.div`
    margin-top: 10px;
    font-weight: 600;
    font-size: 18px;
    color: ${props => props.loadingMessageColor};
`;

const FlexContainer = styled.div`
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const LoadingSpinner = ({ baseColor, centerColor, highlightColor, shadowColor, shadow, width, height, ringSize, aniType, aniDur, id, loadingMessage, trailSmoothing, loadingMessageColor }) => (
    <Overlay>
        <FlexContainer>
        <svg width={width} height={height} id={id} viewBox={"0 0 "+ width + " " + height} xmlns="http://www.w3.org/2000/svg">
            <defs>
                <g> 
                    <linearGradient x1="0%" y1="50%" x2="50%" y2="52.936%" id={`setGradient_${id}`}>
                    <stop stopColor={highlightColor} offset={trailSmoothing ? "20%" : "99%"} />
                    <stop stopColor={baseColor} offset="100%" />
                    </linearGradient>
                </g>
                {shadow !== false &&
                <radialGradient cx="50%" cy="49.9613255%" fx="50%" fy="49.9613255%" r="49.5927734%" id={`circleShadow_${id}`}>
                    <stop stopColor={shadowColor} offset="0%"></stop>
                    <stop stopColor={shadowColor} stopOpacity="0" offset="100%"></stop>
                </radialGradient>
                }
            </defs>
            <g fill="none" fillRule="evenodd">
                <SpinGrad fill={`url(#setGradient_${id})`} aniDur={aniDur} aniType={aniType} cx="50%" cy="50%" r="50%" x="0" y="0">
                {aniType === "SMIL" && 
                <animateTransform 
                    attributeType="xml"
                    attributeName="transform"
                    type="rotate"
                    from="0 0 0"
                    to="360 0 0"
                    dur={aniDur}
                    repeatCount="indefinite" />
                }
                </SpinGrad>
                <circle fill={`url(#circleShadow_${id})`} cx="50%" cy="50%" r="50%"/>
                {ringSize === "1" && <circle fill={centerColor} cx="50%" cy="50%" r="48%" />}
                {ringSize === "2" && <circle fill={centerColor} cx="50%" cy="50%" r="46%" />}
                {ringSize === "3" && <circle fill={centerColor} cx="50%" cy="50%" r="44%" />}
                {ringSize === "4" && <circle fill={centerColor} cx="50%" cy="50%" r="42%" />}
                {ringSize === "5" && <circle fill={centerColor} cx="50%" cy="50%" r="40%" />}
                {ringSize === "6" && <circle fill={centerColor} cx="50%" cy="50%" r="38%" />}
                {ringSize === "7" && <circle fill={centerColor} cx="50%" cy="50%" r="36%" />}
                {ringSize === "8" && <circle fill={centerColor} cx="50%" cy="50%" r="34%" />}
                {ringSize === "9" && <circle fill={centerColor} cx="50%" cy="50%" r="32%" />}
                {ringSize === "10" && <circle fill={centerColor} cx="50%" cy="50%" r="30%" />}
            </g>
        </svg>
        {loadingMessage && 
            <LoadingText 
                loadingMessageColor={loadingMessageColor}>
                {loadingMessage}
            </LoadingText> 
        }
        </FlexContainer>
    </Overlay>
);

LoadingSpinner.propTypes = {
    aniDur: PropTypes.number,
    aniType: PropTypes.oneOf(["SMIL", "CSS", "JS"]),
    baseColor: PropTypes.string,
    centerColor: PropTypes.string,
    height: PropTypes.number,
    highlightColor: PropTypes.string,
    id: PropTypes.string,
    loadingMessage: PropTypes.string,
    loadingMessageColor: PropTypes.string,
    ringSize: PropTypes.oneOf(["1", "2", "4", "5", "6", "7", "8", "9", "10"]),
    shadow: PropTypes.bool,
    shadowColor: PropTypes.string,
    trailSmoothing: PropTypes.bool,
    width: PropTypes.number,
};

LoadingSpinner.defaultProps = {
    aniDur: 1,
    aniType: "CSS",
    baseColor: "#FFDFE8",
    centerColor: "#fff",
    height: 50,
    highlightColor: "#FF618E",
    id: "spinner",
    loadingMessage: "Loading",
    loadingMessageColor: "#9E1080",
    ringSize: "4",
    shadow: false,
    shadowColor: "#000",
    trailSmoothing: false,
    width: 50,
};

export default LoadingSpinner;


