import React from 'react';
import styled, { keyframes } from 'styled-components';

var OSName="Unknown OS";
if (navigator.appVersion.indexOf("Win")!==-1) OSName="Windows";
if (navigator.appVersion.indexOf("Mac")!==-1) OSName="MacOS";
if (navigator.appVersion.indexOf("X11")!==-1) OSName="UNIX";
if (navigator.appVersion.indexOf("Linux")!==-1) OSName="Linux";
if (navigator.userAgent.match(/Android/i)) OSName="Android";

// detect IE8 and above, and edge
var BrowserName="NA";
if (document.documentMode || /Edge/.test(navigator.userAgent)) {
    BrowserName="Edge"
}

const Container = styled.div`
    margin: auto;
    width: 400px;
    @media screen and (min-width: 250px) {width: 250px;height: 270px;}
    @media screen and (min-width: 350px) {width: 350px;height: 200px;}
    @media screen and (min-width: 480px) {width: 480px;height: auto;}
    @media screen and (min-width: 720px) {width: 720px;height: auto;}
    @media screen and (min-width: 934px) {width: 934px;height: auto;}
`;

const GradientLayer = styled.svg`
    clip-path: url(#svgTextPath);
    height: 270px;
    margin-bottom: 50px;
    transform: translateX(0); /* MAGIC!? */
    width: 400px;
    z-index: 5;
    @media screen and (min-width: 250px) {width: 250px;height: 270px;}
    @media screen and (min-width: 350px) {width: 350px;height: 270px;}
    @media screen and (min-width: 480px) {width: 480px;height: 270px;}
    @media screen and (min-width: 720px) {width: 720px;height: 340px;}
    @media screen and (min-width: 934px) {width: 934px;height: 340px;}
`;

const FillLayer = styled.svg`
    fill: #D73664;
    height: 270px;
    margin-bottom: 50px;
    transform: translateX(0); /* MAGIC!? */
    width: 400px;
    z-index: 5;
    @media screen and (min-width: 250px) {width: 250px;height: 270px;}
    @media screen and (min-width: 350px) {width: 350px;height: 270px;}
    @media screen and (min-width: 480px) {width: 480px;height: 270px;}
    @media screen and (min-width: 720px) {width: 720px;height: 340px;}
    @media screen and (min-width: 934px) {width: 934px;height: 340px;}
`;

const Text1 = styled.text`
    font-family: "Avenir Next";
    font-weight: 700;
    text-align: center;
    -webkit-backface-visibility: hidden;
    ${props => OSName === "Windows" && `font-family: "Arial";`}
    @media screen and (min-width: 250px) {font-size: 42px;-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 350px) {font-size: 60px;transform: translate(0px,-80px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 480px) {font-size: 84px;transform: translate(0px,0px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 720px) {font-size: 125px;-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 934px) {font-size: 160px;-webkit-backface-visibility: hidden;}
`;

const Text2 = styled.text`
    font-family: "Avenir Next";
    ${props => OSName === "Windows" && `font-weight: 300;font-family: "Arial";`}
    font-weight: 200;
    font-style: normal;
    -webkit-backface-visibility: hidden;
    @media screen and (min-width: 250px) {font-size: 42px;transform: translate(-188px,-100px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 350px) {font-size: 60px;transform: translate(-159px,-150px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 480px) {font-size: 84px;transform: translate(-121px,-60px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 720px) {font-size: 125px;transform: translate(-56px,-30px);-webkit-backface-visibility: hidden;}
    @media screen and (min-width: 934px) {font-size: 160px;transform: translateX(0);-webkit-backface-visibility: hidden;}
`;

const MoveLeftColor = keyframes`
    0% {
        stop-color: #D73664;
    }
    50% {
        stop-color: #9E1080;
    }
    100% {
        stop-color: #D73664;
    }
`;

const MoveRightColor = keyframes`
    0% {
        stop-color: #9E1080;
    }
    50% {
        stop-color: #D73664;
    }
    100% {
        stop-color: #9E1080;
    }
`;

const GradColorLeft = styled.stop`
    animation: ${MoveLeftColor} 5s linear infinite;
`;

const GradColorRight = styled.stop`
    animation: ${MoveRightColor} 5s linear infinite;
`;


class Hero extends React.Component{
    componentDidMount(){
        window.addEventListener('mouseover',this.handleResize);
    }

    componentWillUnmount(){
        window.removeEventListener('mouseover');
    }

    handleResize = () => {
        this.forceUpdate();
    }

    render(){
        return(
            <div>
                <Container>

            {BrowserName === "Edge" &&     
            <FillLayer>        
                <Text1>
                    <tspan x="0" y="160">RightIndem</tspan>
                </Text1>
                <Text2>
                    <tspan x="254.5" y="300">Design</tspan>
                </Text2>
            </FillLayer>
            }

            {BrowserName !== "Edge" &&
            <div>
            <GradientLayer xmlns="http://www.w3.org/2000/svg" xlinkHref="http://www.w3.org/1999/xlink">
                <defs>
                    <linearGradient x1="0%" y1="50%" y2="50%" id="HeaderGrad" >
                        <GradColorLeft offset="20%" />
                        <GradColorRight offset="80%" />
                    </linearGradient>
                </defs>
                <path d="M0 0h934v359H0z" fill="url(#HeaderGrad)" fillRule="evenodd"/>
            </GradientLayer>

            <svg width="0" height="0" xmlns="http://www.w3.org/2000/svg" key={this.k++} xlinkHref="http://www.w3.org/1999/xlink">   
                <g>
                    <clipPath id="svgTextPath">
                        <Text1>
                        <tspan x="0" y="160">{OSName !== "Android" && `RightIndem` } {OSName === "Android" && <tspan x="20" y="160">RightIndem</tspan>} </tspan>
                        </Text1>
                        <Text2>
                        <tspan x="254.5" y="300">{OSName !== "Android" && `Design` } {OSName === "Android" && <tspan x="265" y="300">Design</tspan>} </tspan>
                        </Text2>
                    </clipPath>
                </g>
            </svg>
            </div>
            }

                </Container>
            </div>
        )
    }
}

export default Hero;


