import React from 'react';
import PropTypes from 'prop-types';
import Heading from '../global/Heading';
import Description from '../global/Description';
import TextTruncate from 'react-text-truncate'; 

const OtherCard = ({ source, PostTitle, PostContent }) => (
    <div>
        <Heading>{PostTitle}</Heading>
        <Description><TextTruncate style={{marginBottom: "0"}} line={3} truncateText="" text={PostContent} /></Description>
    </div>
);


OtherCard.propTypes = {
    source: PropTypes.oneOf(['twitter', 'medium', 'dribbble', 'other', 'blank', 'video']),
};

OtherCard.defaultProps = {
    source: 'other',
};

export default OtherCard;


